#{$collapse-prefix}[dir=rtl] {
    #{$collapse-prefix}-panel-title {
        padding: $collapse-title-padding-tb calc(#{$collapse-icon-margin-r} + #{$collapse-icon-margin-l} + #{$collapse-icon-size}) $collapse-title-padding-tb 0;
    }

    #{$collapse-prefix}-panel-icon {
        left: inherit;
        right: $collapse-icon-margin-l;

        @include icon-size($collapse-icon-size, 0px, 0px, rotate(180deg));
    }
}
